import React, { useState, useEffect } from 'react';
import styled from 'styled-components';

const CoffeelabsectionBlock = styled.div`
    margin-top: 165px;
    height: 200vh;
`;

const Coffeelabsection = () => {

    const [scy, setScy] = useState(0);
    const onScroll = () => {
        setScy(window.scrollY)
    }
    useEffect( () => {
        window.addEventListener("scroll", onScroll)
        window.scrollTo(0,0);
        return () => window.removeEventListener("scroll", onScroll)
    }, [])

    return (
        <CoffeelabsectionBlock style={{marginTop: (scy) ? "164px" : "125px"}}>
            커피랩 페이지
        </CoffeelabsectionBlock>
    );
};

export default Coffeelabsection;