import React, { useCallback, useState, useEffect } from 'react';
import styled, {css} from 'styled-components';
import axios from 'axios';
import Newsitem from './Newsitem';
import { AiOutlineLoading3Quarters } from "react-icons/ai";

const NewssectionBlock = styled.div`
    margin-top: 165px;
    .loadWrap {
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 80vh;
        .loadIcon {
            font-size: 100px; color: #000;
            animation: loadSpin infinite 5s linear;
        }
        @keyframes loadSpin {
            from { transform: rotate(0deg) }
            to { transform: rotate(3turn) }
        }
    }
    .row{
        .category{
            display: flex;
            justify-content: center;
        }
        .articles {
            display: flex;
            flex-wrap: wrap;
        }
    }
`;

const Cate = styled.div `
    padding: 10px 20px; background: #ddd; border-radius: 10px;
    cursor: pointer; margin: 50px 20px;
    ${
        props => props.active && css`background: #333; color: #fff;`
    }
`;

const categories = [
    { name:'all', text:'전체보기' },
    { name:'business', text:'비즈니스' },
    { name:'entertainment', text:'엔터테인먼트' },
    { name:'health', text:'건강' },
    { name:'science', text:'과학' },
    { name:'sports', text:'스포츠' },
    { name:'technology', text:'기술' },
]

const Newssection = () => {

    const [scy, setScy] = useState(0);
    const onScroll = () => {
        setScy(window.scrollY)
    }
    useEffect( () => {
        window.addEventListener("scroll", onScroll);
        window.scrollTo(0,0);
        return () => window.removeEventListener("scroll", onScroll)
    }, [])

    const [category, setCategory] = useState('all');
    const onChange = useCallback(
        (name) => {
            setCategory(name);
        }
    , [])
    const [loading, setLoading] = useState(false);
    const [articles, setArticles] = useState(null);
    const getNews = async () => {
        const query = category==='all' ? '' : `&category=${category}`;
        const response = await axios.get(`https://newsapi.org/v2/top-headlines?country=kr${query}&apiKey=ac2c11fd4649421c9005ccde0f50e889`);
        // console.log(response);
        setArticles(response.data.articles);
        setLoading(true);
    }

    (function() {
        var cors_api_host = 'cors-anywhere.herokuapp.com';
        var cors_api_url = 'https://' + cors_api_host + '/';
        var slice = [].slice;
        var origin = window.location.protocol + '//' + window.location.host;
        var open = XMLHttpRequest.prototype.open;
        XMLHttpRequest.prototype.open = function() {
            var args = slice.call(arguments);
            var targetOrigin = /^https?:\/\/([^\/]+)/i.exec(args[1]);
            if (targetOrigin && targetOrigin[0].toLowerCase() !== origin &&
                targetOrigin[1] !== cors_api_host) {
                args[1] = cors_api_url + args[1];
            }
            return open.apply(this, args);
        };
    })();

    useEffect(
        () => {
            getNews()
        }
    , [category])

    if ( !loading ) {
        return (
            <NewssectionBlock>
                <div className="loadWrap">
                    <AiOutlineLoading3Quarters className="loadIcon" />
                </div>
            </NewssectionBlock>
        );
    } else {
        return (
            <NewssectionBlock style={{marginTop: (scy) ? "164px" : "125px"}}>
                <div className="row">
                    <div className="category">
                        {
                            categories.map( (data, idx) =>
                                <Cate active={category===data.name} key={idx} onClick={()=>onChange(data.name)}>
                                    {data.text}
                                </Cate>
                            )
                        }
                    </div>
                    <div className="articles">
                        {
                            articles && articles.map(
                                (article, idx) => <Newsitem key={idx} article={article} />
                            )
                        }
                    </div>
                </div>
            </NewssectionBlock>
        );
    }

};

export default Newssection;