import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import './Noticesection.scss'
import { Link } from 'react-router-dom';

const NoticesectionBlock = styled.div`
    margin-top: 165px;
    height: 200vh;
    .intro_title {
        background: url(images/news_visual.jpg) no-repeat center;
        background-size: cover;
    }
`;

const Noticesection = () => {

    const [scy, setScy] = useState(0);
    const onScroll = () => {
        setScy(window.scrollY)
    }
    useEffect( () => {
        window.addEventListener("scroll", onScroll);
        window.scrollTo(0,0);
        return () => window.removeEventListener("scroll", onScroll)
    }, [])

    return (
        <NoticesectionBlock className="noticeB" style={{marginTop: (scy) ? "164px" : "125px"}}>
            <div className="contentWrap">
                <div className="intro_title">
                    <h1>공지사항</h1>
                    <div className="intro_info">
                        <p>이디야는 국내 브랜드의 자부심을 지키며</p>
                        <p>대한민국 커피 문화를 이끌어 나갑니다.</p>
                    </div>
                    <ul class="mini_nav">
                        <li><Link>공지사항</Link></li>
                        <li><Link>이벤트</Link></li>
                        <li><Link>홈카페레시피</Link></li>
                        <li><Link>이디야유튜브</Link></li>
                    </ul>
                </div>
            </div>
        </NoticesectionBlock>
    );
};

export default Noticesection;