import React, {useState, useEffect, useCallback} from 'react';
import styled from 'styled-components';
// import {Link} from 'react-router-dom';

const MiseitemBlock = styled.div`
    display: flex;
    justify-content: center;
    .outer {
        flex:1;
        margin: 30px;
        padding: 0 30px;
        width: 900px;
        background: #223d84;
        .inner {
            margin: 30px 0;
            .contentbox {
                margin: 10px; color: #fff;
                border: 2px solid #ccc;
                padding: 30px;
                h4 {
                    font-size: 30px;
                    text-align: center;
                }
                .time { text-align:right; margin: 10px 0; }
                .infowrap {
                    display: flex;
                    flex-wrap: wrap;
                    & > div {
                        width: 46%;
                        background: #4770b2; border: 1px solid #ccc;
                        margin: 2%; padding: 10px; color: #fff;
                    }
                }
                .grade {
                    display: flex;
                    justify-content: space-around; color: #000;
                    background: #888; border: 1px solid #fff;
                    margin: 2%; padding: 10px; font-weight: bold;
                    .grade1 > span {color:#00f}
                    .grade2 > span {color:#0f0}
                    .grade3 > span {color:#ff0}
                    .grade4 > span {color:#f00}
                }
            }
        }
    }
`;

const Miseitem = ({misedata}) => {

    const {
        dataTime, pm10Grade, pm10Value, coGrade, coValue,
        pm25Grade, pm25Value, o3Grade, o3Value, so2Grade,
        so2Value, sidoName, stationName
    } = misedata;

    useEffect ( () => {
        var gcolor = document.getElementsByClassName('gcolor');
        // console.log(gcolor)
        for ( var i=0; i<gcolor.length; i++ ) {
            if ( gcolor[i].innerHTML == "2" ) {
                gcolor[i].style.color = "#0f0"
            } else if ( gcolor[i].innerHTML == "3" ) {
                gcolor[i].style.color = "#ff0"
            } else if ( gcolor[i].innerHTML == "4" ) {
                gcolor[i].style.color = "#f00"
            } else {
                gcolor[i].style.color = "#00f"
            }
        }
    }, [misedata])

    return (
        <MiseitemBlock>
            <div className="outer">
                <div className="inner">
                    <div className="contentbox">
                        <h4>{sidoName} {stationName} 미세먼지</h4>
                        <p className="time">측정시각 : {dataTime}</p>
                        <div className="infowrap">
                            <div className="pm10">
                                <p>
                                    <span>미세먼지(PM10) 농도 : </span>
                                    <span>{pm10Value} [㎍/㎥]</span>
                                </p>
                                <p>
                                    <span>미세먼지(PM10) 등급 : </span>
                                    <span>
                                        <span className="gcolor">{pm10Grade}</span>등급</span>
                                </p>
                            </div>
                            <div className="pm25">
                                <p>
                                    <span>미세먼지(PM2.5) 농도 : </span>
                                    <span>{pm25Value} [㎍/㎥]</span>
                                </p>
                                <p>
                                    <span>미세먼지(PM2.5) 등급 : </span>
                                    <span><span className="gcolor">{pm25Grade}</span>등급</span>
                                </p>
                            </div>
                            <div className="co">
                                <p>
                                    <span>일산화탄소(CO) 농도 : </span>
                                    <span>{coValue} [ppm]</span>
                                </p>
                                <p>
                                    <span>일산화탄소(CO) 등급 : </span>
                                    <span><span className="gcolor">{coGrade}</span>등급</span>
                                </p>
                            </div>
                            <div className="so2">
                                <p>
                                    <span>아황산가스(SO2) 농도 : </span>
                                    <span>{so2Value} [ppm]</span>
                                </p>
                                <p>
                                    <span>아황산가스(SO2) 등급 : </span>
                                    <span><span className="gcolor">{so2Grade}</span>등급</span>
                                </p>
                            </div>
                            <div className="o3">
                                <p>
                                    <span>오존(O3) 농도 : </span>
                                    <span>{o3Value} [ppm]</span>
                                </p>
                                <p>
                                    <span>오존(O3) 등급 : </span>
                                    <span><span className="gcolor">{o3Grade}</span>등급</span>
                                </p>
                            </div>
                        </div>
                        <div className="grade">
                            <p className="gradetitle">등급 안내</p>
                            <p className="grade1">1등급 = <span>좋음</span></p>
                            <p className="grade2">2등급 = <span>보통</span></p>
                            <p className="grade3">3등급 = <span>나쁨</span></p>
                            <p className="grade4">4등급 = <span>매우나쁨</span></p>
                        </div>
                    </div>
                </div>
            </div>
        </MiseitemBlock>
    );
};

export default Miseitem;