import React, {useEffect}  from 'react';
import { HashRouter, Route } from 'react-router-dom';
import Home from './routes/Home';
import Login from './routes/Login';
import Join from './routes/Join';
import Menu from './routes/Menu';
import GiftCard from './routes/GiftCard';
import News from './routes/News';
import Members from './routes/Members';
import CoffeeLab from './routes/CoffeeLab';
import CurtureLab from './routes/CurtureLab';
import Notice from './routes/Notice';
import Mise from './routes/Mise';
import NewsDetail from './routes/NewsDetail';

const App = () => {

  const onKeydown = (e) => {
    if(e.keyCode == 116 || e.ctrlKey == true && (e.keyCode == 82)){
      window.scrollTo(0,0)
    }
  }
  useEffect( () => {
    window.addEventListener("keydown", onKeydown)
    return () => window.removeEventListener("keydown", onKeydown)
  } )

  return (
    <HashRouter>
      <Route path="/" component={Home} exact={true} />
      <Route path="/login" component={Login} />
      <Route path="/join" component={Join} />
      <Route path="/menu" component={Menu} />
      <Route path="/giftcard" component={GiftCard} />
      <Route path="/news" component={News} exact={true} />
      <Route path="/news/detail" component={NewsDetail} />
      <Route path="/mise" component={Mise} exact={true} />
      <Route path="/members" component={Members} />
      <Route path="/coffeelab" component={CoffeeLab} />
      <Route path="/curturelab" component={CurtureLab} />
      <Route path="/notice" component={Notice} />
    </HashRouter>
  );
};

export default App;