import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import './Curturelabsection.scss'
import { Link } from 'react-router-dom';
import {Swiper, SwiperSlide} from 'swiper/react';
import 'swiper/swiper.scss';

const CurturelabsectionBlock = styled.div`
    margin-top: 165px;
    .contentWrap .intro {
        background: url(images/festa_visual.jpg) no-repeat center;
        background-size: cover;
    }
    .festa_bg01::before {
        background: url(images/festa_bg01_01.jpg) center no-repeat;
    }
    .festa_bg01::after {
        background: url(images/festa_bg01_02.jpg) center no-repeat;
    }
    .swiper-slide-prev, .swiper-slide-next {
        z-index=1;
    }
    .swiper-slide-active {
        z-index=2;
    }
    
`;

const Curturelabsection = () => {

    const [scy, setScy] = useState(0);
    const onScroll = () => {
        setScy(window.scrollY)
    }
    useEffect( () => {
        window.addEventListener("scroll", onScroll);
        window.scrollTo(0,0);
        return () => window.removeEventListener("scroll", onScroll)
    }, [])

    return (
        <CurturelabsectionBlock className="curtureB" style={{marginTop: (scy) ? "164px" : "125px"}}>
            <div className="contentWrap">
                <div className="intro">
                    <h1>
                        <img src="images/festa_tt.png" alt="뮤직페스타" />
                    </h1>
                    <p className="info_tt">커피를 사랑하는 사람들의 축제</p>
                    <div className="info">
                        <p>매년 최고의 아티스트들과 이디야 고객님들이 함께 모여</p>
                        <p>즐길 수 있는 공연을 선사합니다.</p>
                    </div>
                    <ul className="mini_nav">
                        <li className="on"><Link>뮤직페스타</Link></li>
                        <li><Link>프로그램 및 시설 안내</Link></li>
                    </ul>
                </div>
                <div className="festa_content">
                    <div className="location">
                        <span>HOME</span>
                        <span>이디야컬쳐랩</span>
                        뮤직페스타
                    </div>
                    <div className="block_con">
                        <div className="con_align">
                            <h2 className="festa_tt">2018 이디야 뮤직페스타</h2>
                            <div className="festa_txt">
                                <p>2011년부터 시작해 8회째를 맞는 '이디야 뮤직페스타'는</p>
                                <p>최정상 뮤지션들과 함께 고객의 성원에 감사의 마음을 전하였습니다.</p>
                            </div>
                            <ul className="festa_info cf">
                                <li>
                                    <img src="images/festa_icon01.gif" alt="이디야 뮤직페스타" />
                                    <div className="festa_info_tt">이디야 뮤직페스타</div>
                                    <p className="festa_info_con">2011년부터 시작된 '이디야 뮤직페스타'는 2018년 약 1만 명의 고객을 무료로 초청하는 등 매년 업그레이드되고 있습니다.</p>
                                </li>
                                <li>
                                    <img src="images/festa_icon02.gif" alt="고객의 사랑" />
                                    <div className="festa_info_tt">고객의 사랑</div>
                                    <p className="festa_info_con">고객의 사랑에 보답하기 위해 축제의 장을 지속적으로 마련하며 고객과의 열정을 함께 합니다.</p>
                                </li>
                                <li>
                                    <img src="images/festa_icon03.gif" alt="망과 성공" />
                                    <div className="festa_info_tt">희망과 성공</div>
                                    <p className="festa_info_con">희망과 성공을 위해 젊음을 모두 소모하고 계시지는 않나요? 이디야 축제에서 젊음, 열정을 통해 일상의 행복을 찾아드립니다.</p>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div className="block_con festa_bg01">
                        <div className="con_align">
                            <h2 className="festa_tt">축제 현장의 열정</h2>
                                <div className="festa_txt">
                                    <p>축제 현장의 식지 않는 젊음과 열정!</p>
                                    <p>고객감사 페스티발 현장의 특별한 순간을 만나보세요.</p>
                                </div>
                        </div>
                    </div>
                    <div className="block_con">
                        <div className="con_align">
                            <Swiper
                                pagination= ".swiper-pagination"
                                paginationClickable = {true}
                                effect= "coverflow"
                                spaceBetween={50}
                                slidesPerView={5}
                                loop={true}
                                centeredSlides={true}
                                onSlideChange={() => console.log('slide change')}
                                onSwiper={(swiper) => console.log(swiper)}
                             >
                                <SwiperSlide>
                                    <img src="images/IMG_1540532411544.png" alt="포스트1" />
                                </SwiperSlide>
                                <SwiperSlide>
                                    <img src="images/IMG_1510211725575.jpg" alt="포스트2" />
                                </SwiperSlide>
                                <SwiperSlide>
                                    <img src="images/IMG_1510211737847.jpg" alt="포스트3" />
                                </SwiperSlide>
                                <SwiperSlide>
                                    <img src="images/IMG_1510211746691.jpg" alt="포스트4" />
                                </SwiperSlide>
                                <SwiperSlide>
                                    <img src="images/IMG_1510211756201.jpg" alt="포스트5" />
                                </SwiperSlide>
                                <SwiperSlide>
                                    <img src="images/IMG_1510211768323.jpg" alt="포스트6" />
                                </SwiperSlide>
                                <SwiperSlide>
                                    <img src="images/IMG_1510211779294.jpg" alt="포스트7" />
                                </SwiperSlide>
                                <SwiperSlide>
                                    <img src="images/IMG_1510211790591.jpg" alt="포스트8" />
                                </SwiperSlide>
                            </Swiper>
                        </div>
                    </div>
                </div>
            </div>
        </CurturelabsectionBlock>
    );
};

export default Curturelabsection;