import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import './Loginsection.scss';

const LoginsectionBlock = styled.div`
    margin-top: 165px;
    background: url(images/login_bg.jpg) no-repeat center;
    width:100%;
    background-size: cover;
    background-attachment: fixed;
`;

const Loginsection = () => {

    const [scy, setScy] = useState(0);
    const onScroll = () => {
        setScy(window.scrollY)
    }
    useEffect( () => {
        window.addEventListener("scroll", onScroll);
        window.scrollTo(0,0);
        return () => window.removeEventListener("scroll", onScroll)
    }, [])

    return (
        <LoginsectionBlock class="loginB" style={{marginTop: (scy) ? "164px" : "125px"}}>
            <div className="login_container">
                <h1>
                    <img src="images/login_tt.png" alt="login" />
                </h1>
                <p className="login_txt">
                    <p>Welcome, Ediya Coffee</p>
                    <p>이디야커피에 오신 것을 환영합니다.</p>
                </p>
                <div className="mem_info">
                    <h2>
                        이디야 회원이셨나요?
                        <p>이디야 멤버스가 새로워졌습니다.</p>
                    </h2>
                    <div className="info_plus">
                        <p>회원님께 더 좋은 서비스를 제공해드리고자,</p>
                        <p>선물하기, 이디야카드, 스마트오더 등의 서비스를 추가하여 더욱 새로워졌습니다.</p>
                        <p>회원님께서 새로운 서비스를 이용하시기 위해서는 본인 인증이 필요합니다.</p>
                        <p>본인 인증은 1회만 진행하며, 인증후, 더 편리하게 서비스를 이용하실 수 있습니다.</p>
                    </div>
                    <Link to="">이디야 멤버스 가입정보 찾기</Link>
                </div>
                <div className="login_box">
                    <div className="login_form">
                        <form method="post" name="login_form">
                            <input type="text" name="user_id" id="user_id" placeholder="이메일을 입력하세요" />
                            <input type="password" name="user_pwd" id="user_pwd" placeholder="비밀번호를 입력하세요" />
                            <div className="email_save">
                                <input type="checkbox" id="email_save" />
                                <label for="email_save">이메일 저장</label>
                            </div>
                            <input type="submit" value="로그인"></input>
                            <div className="join_btn">
                                <Link>회원가입</Link>
                                <Link>이메일 찾기</Link>
                                <Link>비밀번호 찾기</Link>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </LoginsectionBlock>
    );
};

export default Loginsection;