import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import './Homesection.scss';
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css";
import { IoIosArrowForward, IoIosArrowBack } from 'react-icons/io';
import { Link } from 'react-router-dom';
// import cn from 'classnames';

const HomesectionBlock = styled.div`
    margin-top: 165px;
`;
const Article1 = styled.div`
    position: relative;
    .slide {
        height: 621px !important;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        &.slide1 { background-image: url('images/IMG_1620352454803.jpg') };
        &.slide2 { background-image: url('images/IMG_1619999650320.jpg') };
        &.slide3 { background-image: url('images/IMG_1617841130113.jpg') };
        &.slide4 { background-image: url('images/IMG_1618543566896.jpg') };
        &.slide5 { background-image: url('images/IMG_1597725308103.jpg') };
        &.slide6 { background-image: url('images/IMG_1592197905435.jpg') };
        &.slide7 { background-image: url('images/IMG_1617235187683.jpg') };
        &.slide8 { background-image: url('images/IMG_1614214782349.jpg') };
        &.slide9 { background-image: url('images/IMG_1613606229656.jpg') };
    }
    .slick-arrow { 
        position:absolute; top:50%; transform:translateY(-50%);
        background:none; padding:10px; z-index:999;
        width:100px; height:100px; color: #fff;
      &.slick-prev { left:-10px;}
      &.slick-next { right:-10px; }
    }
    .slick-dots {
        position:absolute; bottom:30px; left:50%; transform:translate(-50%); 
        li {
            display: inline-block; padding: 0 10px;
            button {
                width: 10px; height: 10px; border-radius: 50%;
                background: #fff; text-indent: -99999px; overflow: hidden;
            }
          &.slick-active {
              button { background: #243c84; }
          }
        }
    }
`;
const Article2 = styled.div`
    background: url(images/main_beanist_bg.gif) no-repeat center;
    background-size: cover;
    .slick-arrow { 
        position:absolute; top:50%; transform:translateY(-50%);
        background:none; padding:10px; z-index:999;
        width:100px; height:100px; color: #fff;
        
      &.slick-prev { left:-10px; }
      &.slick-next { right:-10px; }
    }
    .slick-dots {
        position:absolute; bottom:-50px; left:50%; transform:translate(-50%); 
        li {
            display: inline-block; padding: 0 10px;
            button {
                width: 10px; height: 10px; border-radius: 50%;
                background: #fff; text-indent: -99999px; overflow: hidden;
            }
          &.slick-active {
              button { background: #90470c; }
          }
        }
    }
`;
const Article3 = styled.div`
    width:100%;
    background: url(images/dream_factory_bg.gif) repeat-y;
    background-size: 100% auto;
    overflow: hidden;
    word-break: keep-all;
    .factory {
        h2 {
            p {
                width: 880px;
                height: 74px;
                background: url(images/dream_factory_txt.png) no-repeat;
                background-size: 100% auto;
            }
        }
    }
`;
const Article4 = styled.div`
    width: 100%;
    background: url(images/block02_bg.jpg) no-repeat;
    background-size: cover;
`;

const Homesection = () => {

    const [scy, setScy] = useState(0);
    const onScroll = () => {
        setScy(window.scrollY)
    }
    useEffect( () => {
        window.addEventListener("scroll", onScroll)
        window.scrollTo(0,0);
        return () => window.removeEventListener("scroll", onScroll)
    }, [])

    const settings1 ={
        dots:true,
        infinite:true,
        speed:600,
        autoplay:true,
        autoplaySpeed:4000,
        slidesToShow:1,
        slidesToScroll:1,
        prevArrow: <IoIosArrowBack />,
        nextArrow: <IoIosArrowForward />
    }
    const settings2 ={
        dots:true,
        arrows:false,
        infinite:true,
        speed:600,
        autoplay:true,
        autoplaySpeed:4000,
        slidesToShow:1,
        slidesToScroll:1,
        prevArrow: <IoIosArrowBack />,
        nextArrow: <IoIosArrowForward />
    }

    return (
        <HomesectionBlock style={{marginTop: (scy) ? "164px" : "125px"}}>
            <div className="homeWrap">
                <Article1 className="article1">
                    <Slider {...settings1}>
                        <div className="slide slide1"></div>
                        <div className="slide slide2"></div>
                        <div className="slide slide3"></div>
                        <div className="slide slide4"></div>
                        <div className="slide slide5"></div>
                        <div className="slide slide6"></div>
                        <div className="slide slide7"></div>
                        <div className="slide slide8"></div>
                        <div className="slide slide9"></div>
                    </Slider>
                </Article1>
                <Article2 className="article2">
                    <h2>
                        <span>BEANIST</span> INFORMATION CARD
                    </h2>
                    <h3>
                        비니스트는 <span>BEAN+Specialist</span>의 합성어로
                        <p>
                            <span>오직 커피만을 생각해온 커피 전문가</span>를 의미합니다.
                        </p>
                    </h3>
                    <Slider {...settings2}>
                        <div className="slide slide1">
                            <ul className="cpack1">
                                <li>
                                    <Link>
                                        <p>
                                            <img src="images/main_beanist_ame01.png" alt="" />
                                        </p>
                                        <p>비니스트 오리지널 아메리카노</p>
                                        <p>Beanist Original Americano</p>
                                    </Link>
                                </li>
                                <li>
                                    <Link>
                                        <p>
                                            <img src="images/main_beanist_ame02.png" alt="" />
                                        </p>
                                        <p>비니스트 마일드 아메리카노</p>
                                        <p>Beanist Mild Americano</p>
                                    </Link>
                                </li>
                                <li>
                                    <Link>
                                        <p>
                                            <img src="images/main_beanist_ame03.png" alt="" />
                                        </p>
                                        <p>비니스트 스페셜 아메리카노</p>
                                        <p>Beanist Special Americano</p>
                                    </Link>
                                </li>
                            </ul>
                        </div>
                        <div className="slide slide2">
                            <ul className="cpack2">
                                <li>
                                    <Link>
                                        <p>
                                            <img src="images/main_beanist_latte01.png" alt="" />
                                        </p>
                                        <p>비니스트 카페 라떼</p>
                                        <p>Beanist Cafe Latte</p>
                                    </Link>
                                </li>
                                <li>
                                    <Link>
                                        <p>
                                            <img src="images/main_beanist_latte02.png" alt="" />
                                        </p>
                                        <p>비니스트 바닐라 라떼</p>
                                        <p>Beanist Vanilla Latte</p>
                                    </Link>
                                </li>
                                <li>
                                    <Link>
                                        <p>
                                            <img src="images/main_beanist_latte03.png" alt="" />
                                        </p>
                                        <p>비니스트 초콜릿 칩 라떼</p>
                                        <p>Beanist Chocolate Chip Latte</p>
                                    </Link>
                                </li>
                                <li>
                                    <Link>
                                        <p>
                                            <img src="images/main_beanist_latte04.png" alt="" />
                                        </p>
                                        <p>비니스트 토피 넛 라떼</p>
                                        <p>Beanist Toffe Nut Latte</p>
                                    </Link>
                                </li>
                            </ul>
                        </div>
                        <div className="slide slide3">
                            <ul className="cpack3">
                                <li>
                                    <Link>
                                        <p>
                                            <img src="images/main_beanist_mix01.png" alt="" />
                                        </p>
                                        <p>모카블렌드 커피믹스</p>
                                        <p>Mocha Blend Coffee Mix</p>
                                    </Link>
                                </li>
                                <li>
                                    <Link>
                                        <p>
                                            <img src="images/main_beanist_mix02.png" alt="" />
                                        </p>
                                        <p>골드블렌드 커피믹스</p>
                                        <p>Gold Blend Coffe Mix</p>
                                    </Link>
                                </li>
                            </ul>
                        </div>
                    </Slider>
                </Article2>
                <Article3 className="article3">
                    <div className="factory">
                        <h2>
                            <p>DREAM FACTORY</p>
                            EDIYA COFFEE
                        </h2>
                        <p>
                            최첨단 시설에서 탄생하는 고품질의 원두로
                            <p>최고의 커피맛을 고객들에게 제공합니다.</p>
                        </p>
                        <div className="df_btn">
                            <Link to="/">드림 팩토리</Link>
                            <Link to="/">드림 물류센터</Link>
                        </div>
                        <img src="images/dream_factory.png" alt="드림팩토리" />
                    </div>
                </Article3>
                <Article4 className="article4">
                    <h2>
                        ALWAYS BESIDE YOU, <span>EDIYA COFFEE</span>
                    </h2>
                    <h3>
                        늘 당신 곁에, 이디야 커피의 다양한 메뉴를 맛보세요.
                    </h3>
                    <Link to="/">메뉴보기</Link>
                </Article4>
            </div>
        </HomesectionBlock>
    );
};

export default Homesection;