import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import cn from 'classnames';
import $ from 'jquery';

const HeaderBloack = styled.div`
    background: #fff;
    padding: 10px 0;
    position: fixed;
    top:0; left:0; width:100%; z-index:99999;
    border-bottom: none;
    .hdtop {
        font-size: 13px; color: #848484;
        .brandbtn {
            float:left;
            margin: 20px 0;
            & > li {
                display: inline-block;
                margin-right: 20px;
                font-weight: bold;
                position: relative;
                &:before {
                    content:'';
                    position: absolute;
                    top: 4px; left: -10px;
                    width: 1px; height: 10px;
                    background: #e0e0e0;
                }
                &:first-child { color: #223c86; }
                &:first-child:before { display: none; }
            }
        }
        .loginjoin {
            float:right;
            margin: 20px 0;
            .ljbtn { display: inline-block;
                & > li {
                    display: inline-block;
                    margin-right: 20px;
                    font-weight: bold;
                    transition: 0.3s;
                    position: relative;
                    &:before {
                        content:'';
                        position: absolute;
                        top: 4px; left: -10px;
                        width: 1px; height: 10px;
                        background: #e0e0e0;
                    }
                    &:first-child:before { display: none; }
                    &:hover { color: #000; }
                }
            }
            .snsbtn {
                display: inline-block;
                & > li {
                    display: inline-block;
                    margin-right: 10px;
                }
            }
        }
    }
    .hdbottom {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .logo { flex: 1 1 202px; z-index:99999; }
        .nav { flex: 0 1 auto; padding-left: 40px;
            .depth1 {
                display: flex;
                justify-content: center;
                & > li {
                    color: #202023;
                    flex: content;
                    padding: 10px 28px;
                    position: relative;
                    cursor: pointer;
                    & > a { 
                        display: block;
                        position: relative;
                    }
                    & > a::before { 
                        content:''; background:#000;
                        width: 0%; height:5px;
                        position: absolute;
                        bottom:-10px; left:50%;
                        transform: translateX(-50%);
                        opacity:0;
                        transition: 0.25s;
                    }
                    &:hover > a::before { 
                        width: 100%; opacity:1;
                    }
                    .depth2 {
                        color: #848484; font-size: 13px;
                        position: absolute;
                        left:50%; top:100%;
                        transform: translateX(-50%);
                        width: calc(100% + 2px); text-align: center;
                        cursor: Default;
                        padding: 20px 0 10px 0;
                        opacity:0;
                        height:0;
                        transition: 0.1s;
                        display: none;
                        &.toggle {
                            opacity:1;
                            height:auto;
                            display: block;
                        }
                        li {
                            padding: 5px 0;
                            a {
                                display: inline-block;
                                padding: 2px 5px;
                                color: #848484;
                                transition: 0.25s;
                                &:hover {
                                    color: #000;
                                    font-weight: bold;
                                }
                            }
                        }
                    }
                }
                &:hover:before {
                    height:260px;
                    opacity:1;
                }
            }
            &.sticky > ul:before {
                position: fixed;
                content:'';
                background: #fff;
                top:125px;
                left:0; right:0;
                height:0px;
                border-bottom: 2px solid #494949;
                opacity:0;
                transition: 0.1s;
            }
            & > ul:before {
                position: fixed;
                content:'';
                background: #fff;
                top:63px; //스크롤 0일때 +62px
                left:0; right:0;
                height:0px;
                border-bottom: 2px solid #494949;
                opacity:0;
                transition: 0.1s;
            }
        }
    }
`;

const Header = () => {

    const [toggle, setToggle] = useState(false);
    const toggleOver = () => { setToggle(true) };
    const toggleOut = () => { setToggle(false) };
    const [scy, setScy] = useState(0);
    const onScroll = () => {
        setScy(window.scrollY)
    }
    useEffect( () => {
        window.addEventListener("scroll", onScroll)
        return () => window.removeEventListener("scroll", onScroll)
    }, [])
    
    if( !scy ) {
        $('nav').addClass('sticky');
        $('.header').css('border-bottom','none')
    } else {
        $('nav').removeClass('sticky');
        $('.header').css('border-bottom','solid 1px #d9d9d9')
        $('.depth1').on("mouseover", function(){
            $('.header').css('border-bottom','none')
        });
            
    }

    return (
        <HeaderBloack className="header">
            <div className="hdtop row cf" style={{display: (scy) ? "none" : "block"}}>
                <ul className="brandbtn">
                    <li><Link to="/">브랜드 사이트</Link></li>
                    <li><Link to="/">기업 사이트</Link></li>
                    <li><Link to="/">창업안내</Link></li>
                </ul>
                <div className="loginjoin">
                    <ul className="ljbtn">
                        <li><Link to="/login">로그인</Link></li>
                        <li><Link to="/join">회원가입</Link></li>
                        <li><Link to="/">매장찾기</Link></li>
                        <li><Link to="/">고객의 소리</Link></li>
                    </ul>
                    <ul className="snsbtn">
                        <li>
                            <Link to="/" className="s1">
                                <img src="images/top_sns01.gif" alt="블로그" />
                            </Link>
                        </li>
                        <li>
                            <Link to="/" className="s2">
                                <img src="images/top_sns02.gif" alt="인스타그램" />
                            </Link>
                        </li>
                        <li>
                            <Link to="/" className="s3">
                                <img src="images/top_sns03.gif" alt="페이스북" />
                            </Link>
                        </li>
                        <li>
                            <Link to="/" className="s4">
                                <img src="images/top_sns04.gif" alt="유튜브" />
                            </Link>
                        </li>
                    </ul>
                    <select>
                        <option>KOREAN</option>
                        <option>ENGLISH</option>
                    </select>
                </div>
            </div>
            <div className="hdbottom row">
                <h1 className="logo">
                    <Link to="/">
                        <img src="images/top_logo.gif" alt="logo" />
                    </Link>
                </h1>
                <nav className="nav">
                    <ul className="depth1" onMouseOver={toggleOver} onMouseOut={toggleOut}>
                        <li>
                            <Link to="/menu">메뉴</Link>
                            <ul className={cn("depth2", {toggle})}>
                                <li><Link to="/menu">음료</Link></li>
                                <li><Link to="/menu">베이커리</Link></li>
                                <li><Link to="/menu">이디야 스낵</Link></li>
                                <li><Link to="/menu">MD</Link></li>
                                <li><Link to="/menu">비니스트</Link></li>
                                <li><Link to="/menu">캡슐 커피</Link></li>
                                <li><Link to="/menu">원두</Link></li>
                            </ul>
                        </li>
                        <li>
                            <Link to="/members">이디야멤버스</Link>
                            <ul className={cn("depth2", {toggle})}>
                                <li><Link to="/members">멤버스 소개</Link></li>
                                <li><Link to="/members">이디야카드</Link></li>
                                <li><Link to="/members">선물하기</Link></li>
                                <li><Link to="/members">이디야오더</Link></li>
                                <li><Link to="/members">FAQ</Link></li>
                            </ul>
                        </li>
                        <li>
                            <Link to="/giftcard">시프트카드</Link>
                            <ul className={cn("depth2", {toggle})}>
                                <li><Link to="/giftcard">기프트카드 소개</Link></li>
                                <li><Link to="/giftcard">잔액 조회</Link></li>
                                <li><Link to="/giftcard">분실신고/환불신청</Link></li>
                                <li><Link to="/giftcard">자주하는 질문</Link></li>
                            </ul>
                        </li>
                        <li>
                            <Link to="/coffeelab">이디야커피랩</Link>
                            <ul className={cn("depth2", {toggle})}>
                                <li><Link to="/coffeelab">이디야커피랩</Link></li>
                                <li><Link to="/coffeelab">이디야커피랩 메뉴</Link></li>
                            </ul>
                        </li>
                        <li>
                            <Link to="/curturelab">이디야컬쳐랩</Link>
                            <ul className={cn("depth2", {toggle})}>
                                <li><Link to="/curturelab">뮤직페스타</Link></li>
                                <li><Link to="/curturelab">프로그램 및 시설 안내</Link></li>
                            </ul>
                        </li>
                        <li>
                            <Link to="/notice">이디야소식</Link>
                            <ul className={cn("depth2", {toggle})}>
                                <li><Link to="/notice">공지사항</Link></li>
                                <li><Link to="/notice">이벤트</Link></li>
                                <li><Link to="/notice">홈카페 레시피</Link></li>
                                <li><Link to="/notice">이디야 유튜브</Link></li>
                                <li><Link to="/notice">이디야 블로그</Link></li>
                                <li><Link to="/notice">이디야 인스타그램</Link></li>
                                <li><Link to="/notice">이디야 페이스북</Link></li>
                            </ul>
                        </li>
                        <li>
                            <Link to="/news">뉴스</Link>
                            <ul className={cn("depth2", {toggle})}>
                                <li><Link to="/news">뉴스</Link></li>
                                <li><Link to="/mise">미세먼지</Link></li>
                            </ul>
                        </li>
                    </ul>
                </nav>
            </div>
        </HeaderBloack>
    );
};

export default Header;