import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import './Menusection.scss'
import { Link } from 'react-router-dom';

const MenusectionBlock = styled.div`
    margin-top: 65px;
    .intro_title {
        background: url(images/menu_intro_visual.jpg) no-repeat center;
        background-size: cover;
    }
    .intro_con { background-size: cover; }
    .members_1 {
        background: url(images/menu_intro_bg01.jpg) center no-repeat;
    }
    .members_2 {
        background: url(images/menu_intro_bg02.jpg) center no-repeat;
    }
    .members_3 {
        background: url(images/menu_intro_bg03.jpg) center no-repeat;
    }
    .members_4 {
        background: url(images/menu_intro_bg04.jpg) center no-repeat;
    }
    .members_5 {
        background: url(images/menu_intro_bg05.jpg) center no-repeat;
    }
`;

const Menusection = () => {

    const [scy, setScy] = useState(0);
    const onScroll = () => {
        setScy(window.scrollY)
    }
    useEffect( () => {
        window.addEventListener("scroll", onScroll);
        window.scrollTo(0,0);
        return () => window.removeEventListener("scroll", onScroll)
    }, [])

    return (
        <MenusectionBlock className="menuB" style={{marginTop: (scy) ? "164px" : "125px"}}>
            <div className="contentWrap">
                <div className="intro_title">
                    <h1>
                        <img src="images/menu_intro_tt.png" alt="메뉴" />
                    </h1>
                    <div className="intro_info">
                        <p>Always beside you, EDIYA COFFEE</p>
                        <p>한 잔의 커피 그 이상의 감동을 경험하세요.</p>
                    </div>
                    <ul class="mini_nav">
                        <li><Link>음료</Link></li>
                        <li><Link>베이커리</Link></li>
                        <li><Link>이디야 스낵</Link></li>
                        <li><Link>MD</Link></li>
                        <li><Link>비니스트</Link></li>
                        <li><Link>이디야 캡슐 커피</Link></li>
                        <li><Link>원두</Link></li>
                    </ul>
                </div>
                <div className="intro_con members_1">
                    <div className="menu_align">
                        <div className="updown_set">
                            <div className="txtbox">
                                <h2 className="txt_title">
                                    <p>음료</p>
                                </h2>
                                <p className="info_a">특별한 경험을 선사합니다.</p>
                                <div className="info_b">
                                    <p>오직 품질에 대한 열정,</p>
                                    <p>이디야의 노력을 음료 한 잔에 가득 담았습니다.</p>
                                </div>
                                <Link>자세히 보기</Link>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="intro_con members_2">
                    <div className="menu_align">
                        <div className="updown_set cf">
                            <div className="txtbox txbxr">
                                <h2 className="txt_title bake">
                                    <p>베이커리</p>
                                </h2>
                                <p className="info_a bake">
                                    <p>갓 구워낸 신선함, 그 맛과 영양을 그대로</p>
                                </p>
                                <div className="info_b bake">
                                    <p>좋은 재료를 엄선해 만든 빵으로 든든한 하루를 시작하세요.</p>
                                </div>
                                <Link className="bake">자세히 보기</Link>
                            </div>                            
                        </div>
                    </div>
                </div>
                <div className="intro_con members_3">
                    <div className="menu_align">
                        <div className="updown_set">
                            <div className="txtbox">
                                <h2 className="txt_title">
                                    <p>MD</p>
                                </h2>
                                <p className="info_a">특별함을 더해줄 상품들</p>
                                <div className="info_b">
                                    <p>이디야커피의 다양한 컬렉션으로 당신의 일상을 더 특별하게!</p>
                                </div>
                                <Link>자세히 보기</Link>
                            </div>                            
                        </div>
                    </div>
                </div>
                <div className="intro_con members_4">
                    <div className="menu_align">
                        <div className="updown_set cf">
                            <div className="txtbox txbxr">
                                <h2 className="txt_title">
                                    <p>비니스트</p>
                                </h2>
                                <p className="info_a">From the Specialist</p>
                                <div className="info_b">
                                    <p>매장에서 즐기던 맛과 향, 그 분위기 그대로 언제 어디서나</p>
                                    <p>간편하게 이디야커피의 다양한 풍미를 즐겨보세요.</p>
                                </div>
                                <Link>자세히 보기</Link>
                            </div>                            
                        </div>
                    </div>
                </div>
                <div className="intro_con members_5">
                    <div className="menu_align">
                        <div className="updown_set">
                            <div className="txtbox">
                                <h2 className="txt_title">
                                    <p>원두</p>
                                </h2>
                                <p className="info_a">최고의 커피를 위해 엄선된 원두사용</p>
                                <div className="info_b">
                                    <p>최상의 원두와 로스팅, 커피연구소의 블렌딩 기법으로</p>
                                    <p>부드러운 맛과 향을 완성합니다.</p>
                                </div>
                                <Link>자세히 보기</Link>
                            </div>                            
                        </div>
                    </div>
                </div>
            </div>
        </MenusectionBlock>
    );
};

export default Menusection;