import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import './Joinsection.scss';

const JoinsectionBlock = styled.div`
    margin-top: 165px;
    .title {
        background: url(images/join_bg.jpg) no-repeat center;
        background-size: cover;
    }
    .join_code .allcheck {
        background: url(images/join_check_on.gif) no-repeat center left;
    }
`;

const Joinsection = () => {

    const [scy, setScy] = useState(0);
    const onScroll = () => {
        setScy(window.scrollY)
    }
    useEffect( () => {
        window.addEventListener("scroll", onScroll);
        window.scrollTo(0,0);
        return () => window.removeEventListener("scroll", onScroll)
    }, [])

    return (
        <JoinsectionBlock className="joinB" style={{marginTop: (scy) ? "164px" : "125px"}}>
            <div className="title">
                <h1>
                    <img src="images/join_tt.png" alt="회원가입" />
                </h1>
                <p>Join Us, Ediya Coffee</p>
                <p>이디야멤버스 가입을 위한 약관 동의 및 본인 인증단계입니다.</p>
            </div>
            <div className="outerbox">
                <div className="innerbox">
                    <form>
                        <legend className="blind">JOIN</legend>
                        <div className="joincom emailform">
                            <h2>이메일을 입력해주세요.</h2>
                            <p className="bugatxt">이메일은 멤버스 로그인 시 아이디로 사용됩니다.</p>
                            <ul className="mailform cf">
                                <li className="email1">
                                    <label className="blind" for="email1"></label>
                                    <input type="text" name="email1" id="email1" placeholder="이메일" />
                                </li>
                                <li className="emailat">@</li>
                                <li className="email2">
                                    <label className="blind" for="email2"></label>
                                    <input type="text" name="email2" id="email2" />
                                </li>
                                <li className="emailselect">
                                    <select name="email" id="email">
                                        <option value="etc">직접입력</option>
                                        <option value="naver.com">naver.com</option>
                                        <option value="daum.net">daum.net</option>
                                        <option value="nate.com">nate.com</option>
                                        <option value="hanmail.com">hanmail.com</option>
                                        <option value="gmail.com">gmail.com</option>
                                    </select>
                                </li>
                            </ul>
                        </div>
                        <div className="joincom">
                            <h2>서비스 이용약관</h2>
                            <h3 className="join_code">
                                <Link to="" className="allcheck">전체동의</Link>
                                 ( ※ 선택동의 사항이 포함되어 있습니다.)
                            </h3>
                            <ul className="join_check_list">
                                <li></li>
                            </ul>
                        </div>
                        <div className="join_btn">
                            <Link to="" className="bluebtn">가입하기</Link>
                        </div>
                    </form>
                </div>
            </div>
        </JoinsectionBlock>
    );
};

export default Joinsection;