import React, { useCallback, useState, useEffect } from 'react';
import styled, {css} from 'styled-components';
import axios from 'axios';
import Miseitem from './Miseitem';
import { AiOutlineLoading3Quarters } from "react-icons/ai";

const MisesectionBlock = styled.div`
    margin-top: 165px;
    min-height: 150vh;
    .loadWrap {
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 80vh;
        .loadIcon {
            font-size: 100px; color: #000;
            animation: loadSpin infinite 5s linear;
        }
        @keyframes loadSpin {
            from { transform: rotate(0deg) }
            to { transform: rotate(3turn) }
        }
    }
    .row{
        h3{
            text-align: center; font-size: 50px;
            color: #223d84; margin-bottom: 30
        }
        .category{
            margin: 0 auto;
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
            max-width: 1000px;
        }
        .miseinfobox {
            display: flex;
            justify-content: center;
        }
    }
`;

const Cate = styled.div `
    padding: 5px 10px; background: #ddd; 
    cursor: pointer; margin: 8px 5px; width:100px; text-align:center;
    ${
        props => props.active && css`background: #223d84; color: #fff;`
    }
`;

const sidos = [
    { name:'서울' },{ name:'부산' },{ name:'대구' },{ name:'인천' },{ name:'광주' },
    { name:'대전' },{ name:'울산' },{ name:'경기' },{ name:'강원' },{ name:'충북' },
    { name:'충남' },{ name:'전북' },{ name:'전남' },{ name:'경북' },{ name:'경남' },
    { name:'제주' },{ name:'세종' }
]

const Misesection = () => {

    const [scy, setScy] = useState(0);
    const onScroll = () => {
        setScy(window.scrollY)
    }
    useEffect( () => {
        window.addEventListener("scroll", onScroll);
        window.scrollTo(0,0);
        return () => window.removeEventListener("scroll", onScroll)
    }, [])

    const [sido, setSido] = useState('서울');
    const onChange = useCallback(
        (name) => {
            setSido(name);
        }
    , [])
    const [loading, setLoading] = useState(false);
    const [miseinfo, setMiseinfo] = useState(null);

    const getMise = async () => {
        const sidoname = sido==='서울' ? '서울' : `${sido}`;
        const url =`http://apis.data.go.kr/B552584/ArpltnInforInqireSvc/getCtprvnRltmMesureDnsty?sidoName=${sidoname}&pageNo=1&numOfRows=1&returnType=json&serviceKey=BlwPga%2Bo1YICKpfxUbl7QML5sbuVc%2Bu5Mk%2FsUUJ8m7CqbJoEcjPVminKhtHsh%2F1sSakVAa6Vhqo4s38iIh%2FcIA%3D%3D&ver=1.0`;

        // const url =`http://cors-anywhere.herokuapp.com/http://apis.data.go.kr/B552584/ArpltnInforInqireSvc/getCtprvnRltmMesureDnsty?sidoName=${sidoname}&pageNo=1&numOfRows=1&returnType=json&serviceKey=BlwPga%2Bo1YICKpfxUbl7QML5sbuVc%2Bu5Mk%2FsUUJ8m7CqbJoEcjPVminKhtHsh%2F1sSakVAa6Vhqo4s38iIh%2FcIA%3D%3D&ver=1.0`;
        
        const response = await axios.get(url);
        console.log(response);
        setMiseinfo(response.data.response.body.items);
        setLoading(true);
    }

    (function() {
        var cors_api_host = 'cors-anywhere.herokuapp.com';
        var cors_api_url = 'https://' + cors_api_host + '/';
        var slice = [].slice;
        var origin = window.location.protocol + '//' + window.location.host;
        var open = XMLHttpRequest.prototype.open;
        XMLHttpRequest.prototype.open = function() {
            var args = slice.call(arguments);
            var targetOrigin = /^https?:\/\/([^\/]+)/i.exec(args[1]);
            if (targetOrigin && targetOrigin[0].toLowerCase() !== origin &&
                targetOrigin[1] !== cors_api_host) {
                args[1] = cors_api_url + args[1];
            }
            return open.apply(this, args);
        };
    })();


    useEffect(
        () => {
            getMise()
        }
    , [sido])

    if ( !loading ) {
        return (
            <MisesectionBlock style={{marginTop: (scy) ? "164px" : "125px"}}>
                <div className="loadWrap">
                    <AiOutlineLoading3Quarters className="loadIcon" />
                </div>
            </MisesectionBlock>
        );
    } else {
        return (
            <MisesectionBlock style={{marginTop: (scy) ? "164px" : "125px"}}>
                <div className="row">
                    <h3 className="misetitle">전국 미세먼지 현황</h3>
                    <div className="category">
                        {
                            sidos.map( (data, idx) =>
                                <Cate active={sido===data.name} key={idx} onClick={()=>onChange(data.name)}>
                                    {data.name}
                                </Cate>
                            )
                        }
                    </div>
                    <div className="miseinfobox">
                        {
                            miseinfo && miseinfo.map(
                                (misedata, idx) => <Miseitem key={idx} misedata={misedata} />
                            )
                        }
                    </div>
                </div>
            </MisesectionBlock>
        );
    }

    
};

export default Misesection;