import React, { useState, useEffect } from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import styled from 'styled-components';

const NewsDetailBlock = styled.div`
    margin-top: 165px;
    padding: 50px 0;
    text-align: center;
    .row {
        h2 { font-size: 25px; }
        img { width: 60%; margin: 30px 0; }
    }
`;

const NewsDetail = (props) => {
    const [scy, setScy] = useState(0);
    const onScroll = () => {
        setScy(window.scrollY)
    }
    useEffect( () => {
        window.addEventListener("scroll", onScroll);
        window.scrollTo(0,0);
        return () => window.removeEventListener("scroll", onScroll)
    }, [])

    const { location, history } = props;

    useEffect(
        () => {
            if (location.state === undefined) {
                history.push('/');
            }
        }
    , [history, location.state])

    if (location.state) {
        return (
            <div>
                <Header />
                <NewsDetailBlock style={{marginTop: (scy) ? "164px" : "125px"}}>
                    <div className="row">
                        <h2>{location.state.title}</h2>
                        <p>
                            <img src={location.state.urlToImage} alt={location.state.title} />
                        </p>
                        <p>{location.state.description}</p>
                    </div>
                </NewsDetailBlock>
                <Footer />
            </div>
        );
    } else {
        return null;
    }
};

export default NewsDetail;